/**
 * 按数组并行执行某一个函数
 * @param arr 传入的数组
 * @param fn 每一项执行的函数
 * @param num 并行数量
 */
export function multiRun(arr, fn, num) {
  var i = num;
  var running = 0;
  var result = [];
  return new Promise(function (resolve, reject) {
    var done = function done(res) {
      running -= 1;
      result.push(res);

      if (arr.length) {
        running += 1;
        fn(arr.shift()).then(done)["catch"](function (e) {
          return reject(e);
        });
      } else if (running === 0) {
        resolve(result);
      }
    };

    while (i > 0 && arr.length) {
      i -= 1;
      running += 1;
      fn(arr.shift()).then(done)["catch"](function (e) {
        return reject(e);
      });
    }
  });
}
export default multiRun;